import { Component, ViewChild } from '@angular/core';
import { ViewController,AlertController,IonicPage,  NavController,Events , ActionSheetController, NavParams } from 'ionic-angular';
import { FormControl,  FormBuilder,  FormGroup,   Validators } from '@angular/forms';
import { Auth,NotificationProvider } from '../../../providers';
import { TranslateService } from '@ngx-translate/core';
import { ImageProvider } from '../../../providers/image/image';
import { Camera } from '@ionic-native/camera';
import { Storage } from '@ionic/storage';
import { IonicSelectableComponent } from 'ionic-selectable';
import { Form } from '../../../types';

@IonicPage()
@Component({
  selector: 'page-forms-create',
  templateUrl: 'forms-create.html',
})
export class FormsCreatePage {
  @ViewChild('fileInput') fileInput;
  @ViewChild('portComponent') portComponent: IonicSelectableComponent;
  public form                  : FormGroup;
  username: string = "";
  public nombre                 : any;
  public imagen		     : any;
  public categoria		     : any;
  idempresa:any;
  public estado		     : any;
  private _ID                  : String;
  public foto              	: any;
  public pageTitle           	: string;
  public image              	: any;

  idafiliado:any='na';
  vcategoria: any=[];

  isDisablednit :any;
  constructor(public alertCtrl: AlertController, public storage: Storage, private _IMAGE       : ImageProvider, public msj: NotificationProvider, public translateService: TranslateService,public viewCtrl: ViewController,public authService:  Auth,public navCtrl 		: NavController,  
    public navParams 	: NavParams,   private _FB          : FormBuilder,         public actionSheetCtrl: ActionSheetController, 
          public events: Events)
  {
     // Define form validation - VERY basic!
     this.form = this._FB.group({
        nombre     : ['', Validators.required],
        categoria     : ['', Validators.required],
        estado     : ['', Validators.required],
        imagen     : ['']
     });


     this.translateService.get('APPX').subscribe((value) => {
      this.idempresa=value
      this.authService.getReg('todos/categoria-formmovil/' + this.idempresa,'frmcats').then((data) => {
        this.vcategoria=data;
       });  
      
  })

    
  }

  ngOnInit() {

  


    this.storage.get('username').then((value) => {
      this.username = value;
  }); 

  this.portNameControl = this._FB.control(null, Validators.required);
 
  this.portForm = this._FB.group({
    portName: this.portNameControl
  
  });


  }

  ionViewDidLoad() : void
  {


    if(this.navParams.get("record")==null)
    {
       
      this.translateService.get('ITEM_CREATE_TITLE').subscribe((value) => {
        this.pageTitle=value;
      });
      
      this.isDisablednit =true
    }
    else {
      
       this._ID 				=	this.navParams.data.record._id;
       this.nombre		=	this.navParams.data.record.nombre;
       this.categoria		=	this.navParams.data.record.categoria;
       this.foto		=	this.navParams.data.record.foto;
       this.image	=	this.navParams.data.record.foto;
       this.imagen 	=	this.navParams.data.record.foto;
       this.estado		=	this.navParams.data.record.estado;
       this.isDisablednit =false
       this.translateService.get('ITEM_UPDATE_TITLE').subscribe((value) => {
        this.pageTitle=value;
      });
    

    }
  }

  manageGallery() : void
  {
    let creat='Actualiza';
    if(this.navParams.get("record")==null)
    {  creat='Crea';
    }

     let   options       : any	     = { 
      idempresa: this.idempresa,
     categoria : this.form.controls['categoria'].value.id,
     nombre : this.form.controls['nombre'].value,
     foto : this.imagen,
     estado : this.form.controls['estado'].value,
     bitacora:{idempresa:this.idempresa,idafiliado:'',email: this.username,permiso:creat,accion:creat+' formulario movil '}
    };
      

     if(this.navParams.get("record")==null)
     {  
          if(options){
            this.authService.createReg('',options,'frmmovils').then((result) => {
              if (!this.form.valid) { return; }
              console.log(result)
              this.viewCtrl.dismiss(result);
          }, (err) => {  
            this.msj.showError3('Error',err.error);
          });
          }
     }   
     else
     {
      if(options){
        this.authService.createReg(this._ID,options,'frmmovils').then((result) => {
           if (!this.form.valid) { return; }
           console.log(result)
           this.viewCtrl.dismiss(result);
       }, (err) => {
        this.msj.showError3('Error',err.error);
       });
    }}
  }

  cancel() {
    this.viewCtrl.dismiss();
  }
  done() {
    if (!this.form.valid) { return; }
    //this.viewCtrl.dismiss(this.form.value);
  }

  
  updateProfileImage() {
    let t1,t2;
    this.translateService.get('SELECCIONE_IMAGEN').subscribe((value) => {
      t1=value;
    });
    this.translateService.get('TOME_IMAGEN').subscribe((value) => {
      t2=value;
    });
    let actionSheet = this.actionSheetCtrl.create({
      title: t1,
      buttons: [//1930
        {
          text: t2,
          handler: () => {
            this.takePhotograph();
          }
        },
        {
          text: t1,
          handler: () => {
            this.selectImage();
          }
        }
      ]
    });
    actionSheet.present();
  }

  takePhotograph() : void
  {
   if (Camera['installed']()) {
     this._IMAGE
     .takePhotograph()
     .then((image)=>
     {
        this.imagen   	= image.toString();
        this.image   		= image.toString();
     })
     .catch((err)=>
     {
        console.log(err);
     });
   }
   else{
     this.fileInput.nativeElement.click();
   }
  }

  
  selectImage() : void
  {
   if (Camera['installed']()) {
     this._IMAGE
     .selectPhotograph()
     .then((image)=>
     {
        this.imagen  	= image.toString();
        this.image   		= image.toString();
     })
     .catch((err)=>
     {
        console.log(err);
     });
   }
   else{
     this.fileInput.nativeElement.click();
   }   
  }
  

  public processWebImage(event): void {
    if(event.target.files && event.target.files[0]){
          const reader: FileReader = new FileReader();
          reader.onload = (readerEvent: any) => {
          this.image = readerEvent.target.result;
          this._IMAGE.generateFromImage(this.image, 200, 200, 0.5, data => {
          this.image = data;
          this.imagen =  data;
      });
   };
      reader.readAsDataURL(event.target.files[0]);
    }
}



//********************************************************************************************* */
port: Form;
portForm: FormGroup;
portNameControl: FormControl;
  onAddForm(event: {
    component: IonicSelectableComponent
  }) {
    this.portNameControl.reset();
    this.portNameControl.setValue(event.component.searchText);
    event.component.showAddItemTemplate();
  }
  
  onSaveForm(event: {
    component: IonicSelectableComponent,
    item: Form
  }) {
    this.portNameControl.setValue(event.item.nombre);
    event.component.showAddItemTemplate();
  }
  



  deleteSelectedItem(op) {
    let itemToDeleteIndex;

    this.vcategoria.forEach((selectedItem, itemIndex) => {
      if (selectedItem._id==op) {
        itemToDeleteIndex = itemIndex;
      }
    });

    this.vcategoria.splice(itemToDeleteIndex, 1);
  }

  onDeleteForm(event: {
    component: IonicSelectableComponent,
    item: Form
  }) {
    let alert = this.alertCtrl.create({ title: 'Seguro de realizar esta operación?',
    buttons: [
      {text: 'Si',
        handler: () => {
          var aa:any;
          aa=event.item
          let recordID 		: string		= aa._id;
          this.authService.deleteReg(recordID+'/'+this.username+ '/' + this.idempresa + '/' + this.idafiliado,'frmcats').then((data) => {
            event.component.deleteItem({id:recordID,nombre:'aaa'});
            this.deleteSelectedItem(recordID);
              
          }, (err) => {
            this.msj.showError3('Error',err.error);
          });
      
        }
      },
      {
        text: 'No',  handler: () => { }
      }
    ]
  });
  alert.present();
  }
  
  addForm(op) {
    let creat='Crea';
     let   options       : any	     = { 
       idempresa: this.idempresa,
     idunidad : op,
     nombre : this.portNameControl.value,
     bitacora:{idempresa:this.idempresa,idafiliado:'',email: this.username,permiso:creat,accion:creat+' formulario movil '}
    };
    if(options){
      this.authService.createReg('',options,'frmcats').then((result) => {

        var aa:any=result;
        let port = new Form({
          id: aa._id,
          nombre: this.portNameControl.value
       
        });
        this.portComponent.addItem(port).then(() => {
          this.portComponent.search(port.nombre);
        });
        this.portNameControl.reset();
        this.portComponent.hideAddItemTemplate();
    }, (err) => {  
      this.msj.showError3('Error',err.error);
    });
    }



  }
  
  saveForm(port: Form,op) {
    let creat='Actualiza';
    let   options       : any	     = { 
      idempresa: this.idempresa,
    idunidad : op,
    nombre : this.portNameControl.value,
    bitacora:{idempresa:this.idempresa,idafiliado:'',email: this.username,permiso:creat,accion:creat+' formulario movil '}
   };
   if(options){
     var aa:any;
     aa=port;
     this.authService.createReg(aa._id,options,'frmcats').then((result) => {
       port.nombre = this.portNameControl.value;
       this.portComponent.hideAddItemTemplate();
   }, (err) => {  
     this.msj.showError3('Error',err.error);
   });
  }
}
  
}



